<template>
  <div class="form-login">
    <el-form ref="form" :model="form">
      <el-form-item>
        <el-input v-model="form.email" placeholder="邮箱地址"></el-input>
      </el-form-item>
      <el-form-item v-if="step==1">
        <el-input v-model="form.code" placeholder="查看邮箱后得到 code"></el-input>
      </el-form-item>
      <el-form-item v-if="step==2">
        <el-input v-model="form.password" placeholder="密码" type="password"></el-input>
      </el-form-item>
      <el-form-item v-if="step==2">
        <el-input v-model="form.password2" placeholder="密码确认" type="password"></el-input>
      </el-form-item>
      <el-form-item>
        <el-row>
          <el-col :span="22">
            <el-button type="primary" size="mini" @click="onSendEmail" v-if="step==0">发送验证码</el-button>
            <el-button type="primary" size="mini" @click="onReset" v-if="step==1">验证验证码</el-button>
            <el-button type="primary" size="mini" @click="onReset" v-if="step==2">修改密码</el-button>
          </el-col>
          <el-col :span="2">
            <el-link type="warning" @click="onJump('login')">登录</el-link>
          </el-col>
        </el-row>
      </el-form-item>
    </el-form>

    <About></About>
  </div>
</template>

<script>
import About from "../commons/About.vue";

export default {
  data() {
    return {
      step: 0,
      form: {
        email: ""
      }
    };
  },
  components: { About },
  methods: {
    onSubmit() {
      if (this.form.email == "") {
        this.$notify.error({
          title: "错误",
          message: "请输入邮箱"
        });
        return;
      }

      this.$http
        .post("/auth/reset_email", this.form)
        .then(res => {
          if (res.data.errno == 0) {
            this.$notify({
              title: "成功",
              message: "我们已经将一封邮件发送到你的邮箱，请输入邮箱中的code",
              type: "success"
            });
            this.step = 1;
            return;
          }
          this.$notify.error({
            title: "错误",
            message: res.data.msg
          });
        })
        .catch(err => {
          window.console.log(err);
        });
    },
    onJump(r) {
      this.$router.replace({ name: r });
    }
  }
};
</script>

<style scoped>
.form-login {
  width: 32%;
  margin: auto;
  padding-top: 20%;
}
</style>